import { types, Instance, SnapshotIn, SnapshotOut } from 'mobx-state-tree';

const ProductItem = types.model('Product', {
    id: types.identifier,
    name: types.string,
    clearName: types.string,
    price: types.number,
    imagesUrls: types.array(types.string),
    quantityLeft: types.string,
});

export default ProductItem;

export type ProductItemSnapshotOut = SnapshotOut<typeof ProductItem>;
export type ProductItemSnapshotIn = SnapshotIn<typeof ProductItem>;
export type ProductItemModel = Instance<typeof ProductItem>;
