import React from 'react';
import withBem from 'hoc/withBem';
import { Badge } from 'components/Badge/Badge';
import { ProductItemModel } from 'stores/product';

import './CatalogListItem.scss';

interface CatalogListItemProps extends ProductItemModel, BemProps {
    mountRef?: React.RefObject<HTMLDivElement>;
    index: number;
    countInCart: number;
    onClick: () => void;
}

const Component: React.FC<CatalogListItemProps> = ({
    bem,
    imagesUrls,
    countInCart,
    name,
    price,
    index,
    quantityLeft,
    onClick,
    mountRef,
}) => {
    const [selected, setSelected] = React.useState(false);
    const isSoldOut = Number(quantityLeft) === 0;
    const canAddMoreItems = countInCart < Number(quantityLeft);

    let badgeType = '';

    if (selected) {
        badgeType = canAddMoreItems ? 'add' : 'disabled';
    } else if (countInCart) {
        badgeType = 'done';
    }

    return (
        <div
            tabIndex={isSoldOut ? -1 : 0}
            className={bem.modify({ 'sold-out': isSoldOut })}
            onBlur={() => setSelected(false)}
            onFocus={() => setSelected(true)}
            onClick={onClick}
            ref={mountRef}
        >
            {isSoldOut ? <Badge className={bem('badge')} type="sold-out" /> : ''}
            <div className={bem('index-badge')}>{index}</div>
            <div className={bem('thumb')} style={{ backgroundImage: `url(${imagesUrls[0]})` }} />
            <div className={bem('description')}>
                <div className={bem.modify({ [badgeType]: Boolean(badgeType) }, 'add-badge')} />
                <div className={bem('description-name')}>{name}</div>
                <div className={bem('price-container')}>
                    <div className={bem('price')}>{Math.floor(Number(price) / 100)} </div>
                    {countInCart ? <div className={bem('count')}>{countInCart}</div> : ''}
                </div>
            </div>
        </div>
    );
};

export const CatalogListItem = withBem(Component, 'CatalogListItem');
