import React from 'react';
import { connectToStore } from 'tools/connect';
import { splitByThousands } from 'tools';
import { StoreType } from 'stores/types';
import { SHOW_ITEMS_MOCK } from 'mocks';
import withBem from 'hoc/withBem';

import { CartListItem } from '../ListItem/CartListItem';
import { MINIMAL_PRICE } from '../Cart';

import './CartList.scss';

interface CartListProps extends StoreType, BemProps {}

const ITEM_WIDTH = 152;

const onScroll = (e: React.UIEvent<HTMLDivElement>) => {
    e.currentTarget.scrollTop = 0;
};

const getOffset = (index: number, itemsCount: number) => {
    //      0,      
    //    ( = 1),      
    // 3- ,     1
    let relatedIndex = Math.max(0, index - 1);

    //    
    relatedIndex = Math.min(Math.max(0, itemsCount - 4), relatedIndex);

    //      
    return relatedIndex * ITEM_WIDTH;
};

const Component: React.FC<CartListProps> = ({ cart, bem }) => {
    const { items } = cart;
    const [selectedItemIndex, setSelectedItemIndex] = React.useState(0);
    const isFirstItemSelected = !selectedItemIndex;
    const isLastItemSelected = selectedItemIndex === items.length - 1;

    const wrapperStyle: React.CSSProperties = {
        transform: `translateY(-${getOffset(selectedItemIndex, items.length)}px)`,
    };

    React.useEffect(() => {
        if (process.env.REACT_APP_ENVIRONMENT === 'development') {
            const stock = {
                ...SHOW_ITEMS_MOCK,
                ...Object.values(SHOW_ITEMS_MOCK).reduce((acc, item) => {
                    const guid = item.guid + Math.random() * 1000;

                    return {
                        ...acc,
                        [guid]: {
                            ...item,
                            guid,
                        },
                    };
                }, {}),
            };

            window.AssistantClient.onData({
                type: 'smart_app_data',
                // eslint-disable-next-line
                smart_app_data: {
                    command: 'SHOW_ITEMS',
                    stock,
                },
            });

            Object.values(stock).map((item) =>
                window.AssistantClient.onData({
                    type: 'smart_app_data',
                    // eslint-disable-next-line
                    smart_app_data: {
                        command: 'ADD_ITEM',
                        item: item.guid,
                        count: Math.floor(Math.random() * 5),
                    },
                }),
            );
        }
    }, []);

    return (
        <div className={bem()}>
            <div className={bem('container')} onScroll={onScroll}>
                <div
                    className={bem.modify({ top: true, hidden: items.length <= 4 || isFirstItemSelected }, 'gradient')}
                />
                <div className={bem('wrapper')} style={wrapperStyle}>
                    {items.map((item, index) => (
                        <CartListItem
                            {...item}
                            onSelect={setSelectedItemIndex}
                            onLessClick={(count: number, shouldRemoveMention: boolean) =>
                                cart.removeFromCart({
                                    itemId: item.product.id,
                                    count,
                                    shouldRemoveMention,
                                })
                            }
                            onMoreClick={() => cart.addToCart(item.product.id)}
                            index={index + 1}
                            key={item.product.id}
                        />
                    ))}
                </div>
                <div
                    className={bem.modify(
                        { bottom: true, hidden: items.length <= 4 || isLastItemSelected },
                        'gradient',
                    )}
                />
            </div>
            <div className={bem('total-price')}>  {splitByThousands(String(cart.totalPrice))} </div>
            {cart.totalPrice < MINIMAL_PRICE ? (
                <div className={bem('minimal-price')}>
                        {Math.ceil(MINIMAL_PRICE - cart.totalPrice)} 
                </div>
            ) : (
                ''
            )}
        </div>
    );
};

export const CartList = withBem(connectToStore(Component), 'CartList');
