import { useObserver } from 'mobx-react-lite';

import { StoreType } from '../stores/types';

let store: StoreType | null = null;

export const setStore = (newStore: StoreType): void => {
    store = newStore;
};

type PropsType = Record<string, any>;

const connect = (selector: Function) => (
    baseComponent: React.FC<StoreType & PropsType>,
): React.FC<StoreType & PropsType> => {
    /* eslint-disable */
    const component = (ownProps: PropsType) => {
        if (store === null) throw new Error('Please, use setStore before your ReactDOM.render call');
        return useObserver(() => {
            return baseComponent({ ...ownProps, ...selector(store, ownProps) });
        });
    };
    /* eslint-enable */
    component.displayName = baseComponent.name;
    return component;
};

export default connect;
export const getStore = (): StoreType | null => store;

export const connectToStore = (component: React.FC<StoreType & any>): React.FC<StoreType & any> =>
    connect((storeData: StoreType): StoreType => storeData)(component);
