import React from 'react';
import { connectToStore } from 'tools/connect';
import { StoreType } from 'stores/types';
import withBem from 'hoc/withBem';
import { routes } from 'stores/ui';

import './HeaderAddress.scss';

interface HeaderAddressProps extends StoreType, BemProps {}

const Component: React.FC<HeaderAddressProps> = ({ user, ui, bem }) => (
    <div className={bem()} tabIndex={0} onClick={() => ui.gotoPage(routes.ADDRESS_SIMPLE)}>
        <div className={bem('icon')} />
        <div className={bem('text')}>{user.address}</div>
    </div>
);

export const HeaderAddress = withBem(connectToStore(Component), 'HeaderAddress');
