import React from 'react';
import withBem from 'hoc/withBem';
import { connectToStore } from 'tools/connect';
import { Button } from 'components/Button/Button';
import { StoreType } from 'stores/types';

import './CartInfo.scss';
import { MINIMAL_PRICE } from '../Cart';

interface CartInfoProps extends BemProps, StoreType {}

const addAddressInfo = (user: StoreType['user']) =>
    user.updateUser({
        location: ' 1,  ',
        phoneNumber: '+7 910 345 67 89',
        name: '',
    });

const Component: React.FC<CartInfoProps> = ({ bem, user, cart }) => {
    const { phoneNumber, address, location, name } = user;
    const hasPersonalData = phoneNumber && address && location && name;

    return (
        <div className={bem()}>
            <div className={bem('badge')}>
                <div className={bem('badge-body')}>
                    <div className={bem('badge-name')}>15-30</div>
                    <div className={bem('badge-desc')}></div>
                </div>
                <div className={bem('badge-kernel')} />
                <div className={bem.modify({ big: true }, 'badge-shadow')} />
                <div className={bem.modify({ small: true }, 'badge-shadow')} />
            </div>
            <div className={bem('address')}>{user.address}</div>
            {hasPersonalData ? (
                <>
                    <div className={bem('personal')}>
                        <div className={bem('personal-location')}>{user.location}</div>
                        <div className={bem('personal-phone-number')}>{user.phoneNumber}</div>
                        <div className={bem('personal-name')}>{user.name}</div>
                    </div>
                    <Button
                        className={bem.modify({ disabled: false }, 'button')}
                        disabled={cart.totalPrice < MINIMAL_PRICE}
                        shouldFocusOnMount
                        onClick={() => cart.makePurchase()}
                    >
                          
                    </Button>
                </>
            ) : (
                <>
                    <div className={bem('ask-personal')}>
                          ,  ,     
                    </div>
                    <Button
                        className={bem.modify({ disabled: false }, 'button')}
                        shouldFocusOnMount
                        onClick={() => addAddressInfo(user)}
                    >
                         
                    </Button>
                </>
            )}
        </div>
    );
};

export const CartInfo = withBem(connectToStore(Component), 'CartInfo');
