import React from 'react';
import { connectToStore } from 'tools/connect';
import { StoreType } from 'stores/types';
import withBem from 'hoc/withBem';
import { splitByThousands } from 'tools';

import './HeaderCart.scss';

interface HeaderCartProps extends StoreType, BemProps {}

const Component: React.FC<HeaderCartProps> = ({ cart, ui, bem }) => (
    <div className={bem()} tabIndex={0} onClick={() => ui.goToCart()}>
        <div className={bem('icon')}>{cart.count > 0 && <span className={bem('badge')}>{cart.count}</span>}</div>
        <div className={bem('price')}>{splitByThousands(String(cart.totalPrice))} </div>
    </div>
);

export const HeaderCart = withBem(connectToStore(Component), 'HeaderCart');
