import React from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import { Catalog } from 'pages/Catalog/Catalog';
import { Cart } from 'pages/Cart/Cart';
import { Status } from 'pages/Status/Status';
import { Promo } from 'pages/Promo/Promo';
import { NoArea } from 'pages/NoArea/NoArea';
import { ScreenSaver } from 'pages/ScreenSaver/ScreenSaver';
import { AddressSimple } from 'pages/AddressSimple/AddressSimple';
import withBem from 'hoc/withBem';
import { routes } from 'stores/ui';
import 'style/global.scss';
import 'normalize.css/normalize.css';

import './Root.scss';

type RootProps = BemProps;

const Component: React.FC<RootProps> = ({ bem }) => {
    //       ,      
    //    ~600X300 px
    let containerStyle = {};
    let wrapperStyle = {};

    if (process.env.REACT_APP_ENVIRONMENT === 'staging') {
        containerStyle = {
            transform: 'scale(0.4)',
            height: '1080px',
            width: '1920px',
        };

        wrapperStyle = {
            transform: 'translate(-580px, -310px)',
        };
    }

    return (
        <div style={wrapperStyle}>
            <div style={containerStyle} className={bem()}>
                <Switch>
                    <Route path={routes.CATALOG}>
                        <Catalog />
                    </Route>
                    <Route path={routes.CART}>
                        <Cart />
                    </Route>
                    <Route path={routes.STATUS}>
                        <Status />
                    </Route>
                    <Route path={routes.ADDRESS_SIMPLE}>
                        <AddressSimple />
                    </Route>
                    <Route path={routes.PROMO}>
                        <Promo />
                    </Route>
                    <Route path={routes.NO_AREA}>
                        <NoArea />
                    </Route>
                    <Route path={routes.SCREEN_SAVER}>
                        <ScreenSaver />
                    </Route>
                    <Redirect to={routes.SCREEN_SAVER} />
                </Switch>
            </div>
        </div>
    );
};

export const Root = withBem(Component, 'Root');
