import { types, applySnapshot, Instance, SnapshotIn, SnapshotOut } from 'mobx-state-tree';

const User = types
    .model('User', {
        address: types.maybeNull(types.string),
        location: types.maybeNull(types.string),
        phoneNumber: types.maybeNull(types.string),
        name: types.maybeNull(types.string),
    })
    .actions((self) => ({
        updateUser: (user: Record<string, string>) => {
            applySnapshot(self, { ...self, ...user });
        },
    }));

export default User;

export type UserSnapshotOut = SnapshotOut<typeof User>;
export type UserSnapshotIn = SnapshotIn<typeof User>;
export type UserModel = Instance<typeof User>;
