import 'core-js';
import React from 'react';
import ReactDOM from 'react-dom';
import { Router } from 'react-router-dom';
import history from 'tools/history';
import store from 'stores';
import { setStore } from 'tools/connect';
import { Root } from 'components/Root/Root';

import 'tools/new-spatial-navigation';

setStore(store);

if (process.env.REACT_APP_ENVIRONMENT === 'development') {
    window.store = store;
    const makeInspectable = require('mobx-devtools-mst'); // eslint-disable-line
    makeInspectable.default(store);
}

const APP_ROOT_ID = 'application_root';

export function renderApp(App: React.FC) {
    ReactDOM.render(
        <Router history={history}>
            <App />
        </Router>,
        document.getElementById(APP_ROOT_ID),
    );
}

renderApp(Root);

if (process.env.REACT_APP_ENVIRONMENT !== 'production' && module.hot) {
    module.hot.accept('./components/Root/Root', () => {
        // eslint-disable-next-line
        const { Root } = require('./components/Root/Root.tsx');
        renderApp(Root);
    });
}
