import { types } from 'mobx-state-tree';
import { USER_MOCK } from 'mocks';

import user from './user';
import ui, { routes } from './ui';
import products from './products';
import cart from './cart';
import { StoreType } from './types';
import casts from './casts';

const Store = types
    .model('Store', {
        ui: types.optional(ui, {}),
        products: types.optional(products, {}),
        cart: types.optional(cart, {}),
        user: types.optional(user, {}),
    })
    .actions((self) => {
        const store = self;

        const afterCreate = function init() {
            if (window.AssistantHost && typeof window.AssistantHost.ready === 'function') {
                console.log('call ready native method');
                window.AssistantHost.ready();
            }

            if (process.env.REACT_APP_ENVIRONMENT === 'development') {
                setTimeout(() => {
                    window.AssistantClient.onData({
                        type: 'smart_app_data',
                        // eslint-disable-next-line @typescript-eslint/camelcase
                        smart_app_data: {
                            command: 'GO_TO_CATALOG',
                            // eslint-disable-next-line @typescript-eslint/camelcase
                            address_string: USER_MOCK.address,
                        },
                    });
                }, 0);
            }
        };

        const onMessage = (message: AssistantSdkCommand): void => {
            const { command } = message.smart_app_data;

            if (command in casts) {
                const castFn = casts[command];

                castFn(store, message.smart_app_data);
            }
        };

        return { onMessage, afterCreate };
    });

const store = Store.create({});

// eslint-disable-next-line no-underscore-dangle
window.__store = store;

function castFromStore(store: StoreType): AppStateInterface {
    return {
        currentScreen: store.ui.page === routes.CATALOG ? 'catalog' : 'cart', // eslint-disable-line
        foodtech: {
            products: Object.keys(store.cart.itemsMap).map((itemId) => ({
                id: itemId,
                quantity: store.cart.itemsMap[itemId].count,
            })),
            totalPrice: {
                value: store.cart.totalPrice,
                discountValue: 0,
            },
            address: {
                // eslint-disable-next-line @typescript-eslint/camelcase
                address_string: store.user.address || undefined,
            },
        },
    };
}

window.AssistantClient = window.AssistantClient || {};
// eslint-disable-next-line no-underscore-dangle
window.__inbox = window.__inbox || [];
// eslint-disable-next-line no-underscore-dangle
window.__outbox = window.__outbox || [];

/**
 * Assistand SDK API implementation:
 */
window.AssistantClient.onStart = function AssistantSdkOnStart() {
    console.log('invoke onStart callback');
};

window.AssistantClient.onData = function AssistantSdkOnMessage(command: AssistantSdkCommand): void {
    // eslint-disable-next-line no-underscore-dangle
    window.__inbox.push(command);
    console.log('server message', command);
    store.onMessage(command);
};

window.AssistantClient.sendData = function AssistantSdkSendMessage(message: any): void {
    // eslint-disable-next-line no-underscore-dangle
    window.__outbox.push({ message, store: castFromStore(store) });

    console.log('sendData', message);

    if (window.AssistantHost && typeof window.AssistantHost.sendData === 'function') {
        window.AssistantHost.sendData(JSON.stringify(message), null);
    }
};

window.AssistantClient.onRequestState = function AssistantSdkOnRequestState(): void {
    console.log('invoke request state callback');
    const state = castFromStore(store);

    if (window.AssistantHost && typeof window.AssistantHost.updateState === 'function') {
        window.AssistantHost.updateState(JSON.stringify(state));
    }
};

export default store;
