import { types, Instance, SnapshotIn, SnapshotOut } from 'mobx-state-tree';

import ProductItem from './product';

const Products = types.model('Products', {
    title: types.optional(types.string, ''),
    items: types.array(ProductItem),
    selectedId: types.maybeNull(types.string),
});

export default Products;

export type ProductsModel = Instance<typeof Products>;
export type ProductsSnapshotOut = SnapshotOut<typeof Products>;
export type ProductsSnapshotIn = SnapshotIn<typeof Products>;
